.app {
  margin: 40px auto;
  width: 800px;
  height: 400px;
}
.header {
    display: flex;
    flex-flow: row;
}

.header .section {
    flex-grow: 1;
    font-size: 18px;
    font-weight: bold;
    color: whitesmoke;
}

.header .section .line {
    padding: 2px 0;
}

.header .section.right {
    text-align: right;
}
.playground {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.playground:after {
    content: '';
    display: block;
    position: absolute;
    top: 0px; left: 399px;
    height: 100%; width: 0px;
    border: dashed 1px whitesmoke;
}
.stick {
    position: absolute;
    width: 20px;
    height: 80px;
    background-color: whitesmoke;
}
.ball {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: whitesmoke;
}
.banner-wrapper {
    position: absolute;
    left: 0; top: 0;
    z-index: 999;
    width: 100%; height: 100%;
    background-color: rgba(0,0,0,0.5);
}

.banner {
    width: 300px; height: 200px;
    background-color: black;
    border: solid 2px whitesmoke;
    margin: 100px auto;
}

.banner .message {
    color: whitesmoke;
    margin: 90px auto;
    text-align: center;
}
body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
  background-color: #455A64;
}
